
.error-message {
		display: none;
		font-size: 10px;
		color: #f00;
	}

.invalid {
	border-color: red;
}

.invalid-msg {
	color: red;
	display: block;
}

.valid {
	border-color: green;
}

.delete {
	display: none;
}


.bg-color--white {
	background: #fff;
	box-shadow: 0 0 1px #c0c0c0;
	padding: 15px;

}

.content-form,
.form-view-winners {
	background: #fff;
	border-radius: 5px;
	margin: 15px auto;
	padding: 20px;
	width: 60%;
}

.form-group {
	position: relative;
}

.fa-check:before {
	position: absolute;
	right: 10px;
	top: 34px;
	color: green;
}

.form-group.invalid {
	input {
		border: 1px solid red;
	}

	.error-message {
		display: block;
	}
}





